import React, { useState, useEffect } from "react";

const URL = "https://api.github.com/users/";

const loadJSON = (key) => {
  return JSON.parse(localStorage.getItem(key));
};
const saveJSON = (key, data) => {
  localStorage.setItem(key, JSON.stringify(data));
};

export default function UserInfo({ login }) {
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  console.log(login, data, error, loading);

  useEffect(() => {
    if (!login) return;
    if (data && data.login === login) return;
    setError(null);
    const localData = loadJSON(login);
    if (localData && localData.login === login) {
      setData(localData);
      return;
    }
    async function getData() {
      try {
        setLoading(true);
        const res = await fetch(URL + login);
        const data = await res.json();
        setLoading(false);
        if (!data.login) {
          setError(data);
          setData(null);
        } else {
          setData(data);
          const { login, avatar_url, name, location, created_at, html_url } =
            data;
          saveJSON(login, {
            login,
            avatar_url,
            name,
            location,
            created_at,
            html_url,
          });
        }
      } catch (e) {
        setError(e);
      }
    }
    getData();
  }, [login]);

  if (loading) return <h1>loading...</h1>;
  if (error) return <pre>{JSON.stringify(error, null, 2)}</pre>;
  if (!data) return null;

  return (
    <div className="githubUser">
      <img src={data.avatar_url} alt={data.login} style={{ width: 200 }} />
      <div>
        <h1>{data.login}</h1>
        {data.name && <p>{data.name}</p>}
        {data.location && <p>{data.location}</p>}
        {data.created_at && <p>{data.created_at}</p>}
        {data.html_url && <p>{data.html_url}</p>}
      </div>
    </div>
  );
}
